<template>
	<view class="publish-form">
		<u-form :model="form" ref="uForm" label-width="150">
			<u-form-item label="生活标题" prop="link">
				<u-input v-model="form.link" placeholder="生活标题" />
			</u-form-item>
			<u-form-item label="生活图片" prop="img">
				<uni-file-picker v-model="imageValue" file-mediatype="image" mode="grid" :limit="1" @success="success"></uni-file-picker>
				<text style="color: #D3D3D3;">美好照片</text>
			</u-form-item>
			<u-form-item label="生活描述" prop="desc">
				<u-input v-model="form.desc" type="textarea" placeholder="生活描述" />
			</u-form-item>
		</u-form>
		<view style="margin-top: 50rpx;">
			<u-button type="primary" @click="submit">记录</u-button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				selectShow: false,
				selectTimeShow: false,
				taskLists: [{
						value: '1',
						label: '拼多多'
					},
					{
						value: '2',
						label: '淘宝'
					},
					{
						value: '3',
						label: '小程序'
					},
					{
						value: '4',
						label: '抖音'
					},
					{
						value: '5',
						label: '快手'
					},
					{
						value: '3',
						label: '自定义'
					}
				],
				timeLists: [{
						value: '60',
						label: '1小时'
					},
					{
						value: '180',
						label: '3小时'
					},
					{
						value: '1440',
						label: '24小时'
					}
				],
				form: {
					type: '',
					originType: '',
					desc: '',
					link: '',
					img: '',
					score: '',
					time: '',
					num: '',
				},
				imageValue: [],
				rules: {
					type: [{
						required: true,
						message: '请选择任务分类',
						trigger: ['change', 'blur'],
					}],
					originType: [{
						required: true,
						message: '请输入自定义分类',
						trigger: ['change', 'blur'],
					}],
					score: [{
						required: true,
						message: '请设置积分奖励',
						trigger: ['change', 'blur'],
					}],
					time: [{
						required: true,
						message: '请设置任务完成时限',
						trigger: ['change', 'blur'],
					}],
					num: [{
						required: true,
						message: '请设置任务数量',
						trigger: ['change', 'blur'],
					}],
					img: [{
						validator: (rule, value, callback) => {
							if (this.form.img || this.form.link) {
								callback()
								return true
							} else {
								callback(new Error('生活标题和图片至少填写一个'))
								return false
							}
						},
						message: '生活标题和图片至少填写一个',
						trigger: ['change', 'blur'],
					}],
					link: [{
						validator: (rule, value, callback) => {
							if (this.form.img || this.form.link) {
								callback()
								return true
							} else {
								callback(new Error('生活标题和图片至少填写一个'))
								return false
							}
						},
						message: '生活标题和图片至少填写一个',
						trigger: ['change', 'blur'],
					}]
				}
			};
		},
		methods: {
			selectConfirm(e) {
				this.form.type = e[0].label
			},
			selectTimeConfirm(e) {
				this.form.time = e[0].label
			},
			success(e) {
				this.form.img = e.tempFilePaths[0]
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						if (this.$util.store.userInfo.score < (this.form.score * this.form.num)) {
							this.$util.showToast('积分不足，您当前的可用积分为：' + this.$util.store.userInfo.score + '，快去做任务吧！')
							return
						} else if (this.$util.store.userInfo.credit < 60) {
							this.$util.showToast('您的信用分过低，不能发布任务哦，请先通过每日登录获取信用分吧')
							return
						} else {
							this.form.type = this.form.originType || this.form.type
							this.$util.http('addTask', Object.assign(this.form, {
								time: this.form.time.slice(0,-2) * 60,
								num: 100,
								type: '11'
							})).then(data => {
								this.$util.showToast('记录成功', '', () => {
									uni.switchTab({
										url: `/pages/index/index`
									})
								})
							})
						}
					}
				});

			},
			// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
			onReady() {
				this.$refs.uForm.setRules(this.rules);
			}
		}
	}
</script>

<style lang="scss">
	.publish-form {
		padding: 20rpx 30rpx;
	}
</style>
